.c-hero-banner {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
  padding: 12rem 2rem;
  min-height: 560px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.c-hero-banner:before {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(51, 51, 51, 0.45);
  z-index: 0;
}

body.home .c-hero-banner {
  height: 100vh;
}

.c-hero-banner__wrap {
  position: relative;
  z-index: 1;
}

.c-hero-banner__wrap--contained {
  max-width: 90rem;
  margin: 0 auto;
  width: 100%;
}

