  margin-top: 3.5rem;
}

.c-hero-banner__icon {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 25px;
}

.c-hero-banner__supporting {
  display: grid;
  grid-template-columns: 80px auto auto auto auto auto auto;
  grid-template-areas: "avatar author published updated";
  grid-gap: .5rem 2rem;
  align-items: center;
  color: #F9F9F9;
  margin-top: 3.5rem;
}

